import"core-js/modules/es.array.every";import"core-js/modules/es.array.includes";import"core-js/modules/es.regexp.exec";import"core-js/modules/es.string.includes";import"core-js/modules/es.string.split";import{getNbrOfErrors}from"./getNbrOfErrors";/**
 * @typedef {import("../types").Hangman} Hangman
 * @typedef {import("../types").HangmanStatus} HangmanStatus
 *
 * Helper function that takes a Hangman state and returns whether the
 * the current status is `won`, `lost` or `playing`
 * @param {Hangman} state
 * @return {HangmanStatus}
 */export function getStatus(state){return getNbrOfErrors(state)>=state.maxGuesses?"lost":state.guesses.includes(state.answer)?"won":state.answer.split("").every(function(letter){return state.guesses.includes(letter)})?"won":"playing"}