import"core-js/modules/es.array.filter";import"core-js/modules/es.array.includes";import"core-js/modules/es.string.includes";/**
 * @typedef {import("../types").Hangman} Hangman
 *
 * Helper function that takes a Hangman state and returns the total number
 * of errors in the made guesses
 * @param {Hangman} state - A Hangman game state
 * @returns {number} The number of made guesses that were errors
 */export function getNbrOfErrors(state){return state.guesses.filter(function(guess){return 1===guess.length?!state.answer.includes(guess):state.answer!==guess}).length}